.photos {
  $gap: 20px;
  display: flex;
  justify-content: space-between;
  
  &__img {
    width: calc(50% - 10px);
    height: 256px;
    object-fit: cover;
    object-position: center;
  }

  &_wide_left {
    .photos__img {
      
      &:first-child {
        width: calc(67% - #{$gap / 2});
      }
      &:last-child {
        width: calc(33% - #{$gap / 2});
      }
    }
  }

  &_wide_right {
    .photos__img {
      
      &:first-child {
        width: calc(33% - #{$gap / 2});
      }
      &:last-child {
        width: calc(67% - #{$gap / 2});
      }
    }
  }

  @media screen and (max-width: $small-screen-breakpoint) {

   & {
     display: flex;
    
     &__img {
       flex-basis: 50%;
     }
    }

  }
}  
